import {createLibp2p} from "libp2p";

import { promises as fs } from 'fs';
import * as path from 'path';

import {Libp2p} from 'libp2p';
import {EventEmitter} from 'events';
import {tcp} from "@libp2p/tcp";
import {bootstrap} from "@libp2p/bootstrap";

import {noise} from "@chainsafe/libp2p-noise";
import {yamux} from "@chainsafe/libp2p-yamux";
import {mplex} from "@libp2p/mplex";
import {autoNAT} from "@libp2p/autonat";
import {dcutr} from "@libp2p/dcutr";
import {identify} from "@libp2p/identify";
import * as libp2pInfo from "libp2p/version";
import {keychain} from "@libp2p/keychain";
import {ping} from "@libp2p/ping";
import {kadDHT, removePrivateAddressesMapper} from "@libp2p/kad-dht";
import {ipnsValidator} from "ipns/validator";
import {ipnsSelector} from "ipns/selector";
import {uPnPNAT} from "@libp2p/upnp-nat";
import {createHelia} from "helia";
import {unixfs} from "@helia/unixfs";
import {config} from "./EnvConfig.js";

export interface Data {
    nodeAddress: string[];
    libp2p: Libp2p
}

EventEmitter.defaultMaxListeners = 2000; // Or any number that suits your application's needs

export async function createConfig<T>(
    additionalServices: any,
    bootstrapAddrList?: string[],
): Promise<T> {

    bootstrapAddrList = bootstrapAddrList || process.env.BOOTSTRAP?.split(',') || ["bootstrap"];

    //add public network for IPNS support
    bootstrapAddrList.push("/dnsaddr/bootstrap.libp2p.io/p2p/QmNnooDu7bfjPFoTZYxMNLWUQJyrVwtbZg5gBMjTezGAJN");
    bootstrapAddrList.push("/dnsaddr/bootstrap.libp2p.io/p2p/QmQCU2EcMqAqQPR2i9bChDtGNJchTbq5TbXJJ16u19uLTa");
    bootstrapAddrList.push("/dnsaddr/bootstrap.libp2p.io/p2p/QmbLHAnMoJPWSCR5Zhtx6BHJX9KiKNN6tpvbUcqanj75Nb");
    bootstrapAddrList.push("/dnsaddr/bootstrap.libp2p.io/p2p/QmcZf59bWwK5XFi76CZX8cbJ4BhTzzA3gU1ZjYZcYW3dwt");

    const libp2pOptions: any = {
        connectionEncryption: [
            noise()
        ],
        streamMuxers: [
            yamux(),
            mplex()
        ],
        transports: [
            tcp(),
        ],
        peerDiscovery: [
            bootstrap({
                list: bootstrapAddrList,
                timeout: 0,
            }),
        ],
        services: {
            autoNAT: autoNAT(),
            dcutr: dcutr(),
            identify: identify({
                agentVersion: `${libp2pInfo.name}/${libp2pInfo.version} UserAgent=meta-getaway`
            }),
            keychain: keychain(),
            ping: ping(),
            aminoDHT: kadDHT({
                protocol: '/ipfs/kad/1.0.0',
                peerInfoMapper: removePrivateAddressesMapper,
                clientMode: false,
                validators: {
                    ipns: ipnsValidator
                },
                selectors: {
                    ipns: ipnsSelector
                }
            }),
            upnp: uPnPNAT(),
            ...additionalServices
        }
    }
    return libp2pOptions;
}

async function watchIPNS(ipnsName: string) {
    let lastCid: string | null = null;

    while (true) {
        try {
            const cid = await this.ipfs.name.resolve(ipnsName);
            if (cid !== lastCid) {
                console.log(`IPNS name ${ipnsName} has changed. New CID: ${cid}`);
                lastCid = cid;
            }
        } catch (error) {
            console.error(`Failed to resolve IPNS name ${ipnsName}: ${error}`);
        }

        // Wait for a while before the next check
        await new Promise(resolve => setTimeout(resolve, 5000));
    }
}

async function startGateway() {
    this.libp2p = await createLibp2p(await createConfig({}));
    this.ipfs = await createHelia({libp2p: this.libp2p});
    this.fs = unixfs(this.ipfs);

    //Download a folder from IPFS (IPNS cid) and move it to the local filesystem
    const cid = config.IPNS; // Replace with your IPNS CID
    const localPath = config.TARGET_FOLDER; // Replace with your local path

    for await (const file of this.fs.get(cid)) {
        if (file.type === 'file') {
            const filePath = path.join(localPath, file.path);
            await fs.writeFile(filePath, new Uint8Array(file.content));
            console.log(`File ${file.path} has been downloaded to ${filePath}`);
        }
    }

}

startGateway().catch(console.error);
